.topbar {
  position: fixed;
  top: 0;
  right: 0;
  left: var(--sidebar-width);
  height: var(--topbar-height);
  background: var(--bg-surface);
  border-bottom: 1px solid var(--border-light);
  display: flex;
  align-items: center;
  padding: 0 20px;
  z-index: 50;
}

.topbar-spacer {
  flex: 1;
}

.topbar-actions {
  display: flex;
  align-items: center;
  gap: 14px;
}

.topbar-profile {
  background: none;
  border: none;
  cursor: pointer;
  color: var(--text-secondary);
  padding: 6px;
  border-radius: 50%;
}

.topbar-profile:hover {
  background: var(--bg-main);
  color: var(--text-primary);
}

.profile-icon {
  width: 22px;
  height: 22px;
}

.topbar-logout {
  background: var(--danger);
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: var(--radius);
  cursor: pointer;
  font-weight: 600;
}

.topbar-logout:hover {
  opacity: 0.9;
}

.profile-dropdown {
  position: absolute;
  top: 48px;
  right: 0;
  width: 180px;
  background: var(--bg-surface);
  border: 1px solid var(--border-light);
  border-radius: var(--radius);
  box-shadow: var(--shadow-sm);
  display: flex;
  flex-direction: column;
  z-index: 100;
}

.dropdown-item {
  background: none;
  border: none;
  padding: 10px 12px;
  text-align: left;
  cursor: pointer;
  font-size: 14px;
  color: var(--text-primary);
}

.dropdown-item:hover {
  background: var(--bg-main);
}

.dropdown-divider {
  height: 1px;
  background: var(--border-light);
  margin: 4px 0;
}

.dropdown-item.danger {
  color: var(--danger);
}

.topbar-upgrade {
  background: linear-gradient(
    135deg,
    var(--accent),
    #1d4ed8
  );
  color: white;
  border: none;
  padding: 8px 14px;
  border-radius: var(--radius);
  cursor: pointer;
  font-weight: 600;
  font-size: 14px;
  box-shadow: 0 4px 10px rgba(37, 99, 235, 0.25);
  transition: all 0.2s ease;
}

/* hover */
.topbar-upgrade:hover {
  transform: translateY(-1px);
  box-shadow: 0 6px 14px rgba(37, 99, 235, 0.35);
}

/* active click */
.topbar-upgrade:active {
  transform: translateY(0);
  box-shadow: 0 3px 8px rgba(37, 99, 235, 0.25);
}

.dropdown-section {
  padding: 8px 12px;
}

.dropdown-label {
  display: block;
  font-size: 12px;
  color: var(--text-secondary);
  margin-bottom: 4px;
}

.dropdown-select {
  width: 100%;
  padding: 6px 8px;
  border-radius: var(--radius);
  border: 1px solid var(--border-light);
  background: var(--bg-surface);
  font-size: 13px;
  cursor: pointer;
}

.topbar-plan {
  font-size: 13px;
  font-weight: 600;
  margin-right: 6px;
}

.plan-badge {
  padding: 6px 10px;
  border-radius: 999px;
  border: 1px solid var(--border-light);
  background: var(--bg-main);
}

.plan-badge.free {
  color: #92400e;
  background: #fffbeb;
  border-color: #fde68a;
}

.plan-badge.pro {
  color: #065f46;
  background: #ecfdf5;
  border-color: #6ee7b7;
}

/* =====================================================
   TOPBAR  RESPONSIVE SAFE OVERRIDES
   ===================================================== */

@media (max-width: 1024px) {

  /* Topbar becomes full-width */
  .topbar {
    left: 0;
  }

  /* Hamburger button */
  .topbar-hamburger {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 40px;
    height: 40px;

    background: none;
    border: none;
    border-radius: 10px;

    cursor: pointer;
    margin-right: 8px;
  }

  .topbar-hamburger:hover {
    background: var(--bg-main);
  }

  .topbar-hamburger span {
    width: 20px;
    height: 2px;
    background: var(--text-primary);
    display: block;
    position: relative;
  }

  .topbar-hamburger span::before,
  .topbar-hamburger span::after {
    content: "";
    position: absolute;
    left: 0;
    width: 20px;
    height: 2px;
    background: inherit;
  }

  .topbar-hamburger span::before {
    top: -6px;
  }

  .topbar-hamburger span::after {
    top: 6px;
  }
}

@media (max-width: 768px) {

  .topbar {
    padding: 0 12px;
  }

  .topbar-actions {
    gap: 10px;
  }

  .topbar-upgrade {
    padding: 6px 10px;
    font-size: 13px;
  }

  .topbar-logout {
    padding: 6px 10px;
    font-size: 13px;
  }
}

.topbar-hamburger {
  display: none;
  background: none;
  border: none;
  font-size: 22px;
  cursor: pointer;
  color: var(--text-primary);
}

/* show hamburger on tablet + mobile */
@media (max-width: 1024px) {
  .topbar-hamburger {
    display: block;
  }
}

/*/* =====================================================
   TOPBAR  MOBILE / TABLET (SINGLE ROW, LOCKED ORDER)
   ===================================================== */

@media (max-width: 1024px) {

  /* Full-width topbar */
  .topbar {
    left: 0;
    padding: 0 12px;
    display: flex;
    align-items: center;
  }

  /* Hamburger always left */
  .topbar-hamburger {
    display: block;
    margin-right: 10px;
    flex-shrink: 0;
  }

  /* Actions stay on ONE LINE */
  .topbar-actions {
    display: flex;
    align-items: center;
    gap: 10px;
    flex-wrap: nowrap;              /*  critical */
    width: 100%;
  }

  /* Plan badge stays inline */
  .topbar-plan {
    white-space: nowrap;
    font-size: 12px;
  }

  .plan-badge {
    padding: 5px 10px;
    font-size: 12px;
  }

  /* Profile icon pinned to far right */
  .topbar-profile {
    margin-left: auto;              /*  key line */
    flex-shrink: 0;
  }

  /* Optional: slightly smaller upgrade button */
  .topbar-upgrade {
    padding: 6px 10px;
    font-size: 13px;
  }
}
