/* =========================
   BASE LAYOUT
========================= */

.profile-page {
  max-width: 900px;
  margin: 40px auto;
  padding: 0 20px;
  font-family: "Inter", system-ui, -apple-system, sans-serif;
}

.profile-card {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 14px;
  padding: 28px;
  margin-bottom: 24px;
}

.profile-card h2,
.profile-card h3 {
  margin: 0 0 8px;
  font-weight: 600;
  color: #111827;
}

.profile-subtitle,
.muted {
  color: #6b7280;
  font-size: 14px;
  line-height: 1.5;
}

/* =========================
   FIELDS
========================= */

.profile-field {
  margin-top: 20px;
}

.profile-field label {
  display: block;
  font-size: 13px;
  font-weight: 500;
  color: #374151;
  margin-bottom: 6px;
}

.profile-input,
.profile-card input {
  width: 100%;
  padding: 10px 12px;
  font-size: 14px;
  border-radius: 8px;
  border: 1px solid #d1d5db;
  outline: none;
  transition: border-color 0.2s, box-shadow 0.2s;
}

.profile-input:focus,
.profile-card input:focus {
  border-color: #6366f1;
  box-shadow: 0 0 0 3px rgba(99, 102, 241, 0.15);
}

.profile-readonly {
  padding: 10px 12px;
  background: #f9fafb;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
  font-size: 14px;
  color: #111827;
}

.profile-hint {
  display: inline-block;
  margin-top: 6px;
  font-size: 12px;
  color: #10b981;
}

/* =========================
   ERROR BOX
========================= */

.error-box {
  margin-top: 12px;
  padding: 10px 12px;
  border-radius: 8px;
  font-size: 13px;
  background: #fef2f2;
  color: #b91c1c;
  border: 1px solid #fecaca;
}

/* =========================
   DELETE / DANGER ZONE
========================= */

.profile-card.danger {
  border: 1px solid #fee2e2;
  background: #fff;
}

.profile-card.danger h3 {
  color: #b91c1c;
}

.profile-card.danger p {
  margin-top: 8px;
}

/* =========================
   BUTTONS
========================= */

/* =========================
   BUTTON SYSTEM (CLEAN)
========================= */

button {
  appearance: none;
  border-radius: 8px;           /* unified radius */
  padding: 10px 18px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.2;
  cursor: pointer;
  border: 1px solid transparent;
  transition:
    background-color 0.2s ease,
    color 0.2s ease,
    border-color 0.2s ease,
    box-shadow 0.2s ease,
    transform 0.15s ease;
}

button:disabled {
  opacity: 0.55;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* ---------- PRIMARY (Verify Code) ---------- */
button.primary {
  background-color: #4f46e5;
  color: #ffffff;
  margin-top: 10px;
  padding: 5px;
}

button.primary:hover {
  background-color: #4338ca;
  box-shadow: 0 6px 14px rgba(79, 70, 229, 0.25);
  transform: translateY(-1px);
}

/* ---------- OUTLINE (Send verification code) ---------- */
button.outline-btn {
  background-color: transparent;
  border-color: #dc2626;
  color: #dc2626;
}

button.outline-btn:hover {
  background-color: #fef2f2;
  box-shadow: 0 4px 12px rgba(220, 38, 38, 0.18);
  transform: translateY(-1px);
}

/* ---------- DANGER (Final delete) ---------- */
button.danger-btn {
  background-color: #dc2626;
  color: #ffffff;
  margin-top: 15px;
  padding: 5px;
}

button.danger-btn:hover {
  background-color: #b91c1c;
  box-shadow: 0 6px 16px rgba(220, 38, 38, 0.3);
  transform: translateY(-1px);
}


/* =========================
   OTP INPUT SPACING
========================= */

.profile-card.danger input {
  margin-top: 12px;
  letter-spacing: 3px;
  font-weight: 500;
  text-align: center;
}

/* =========================
   RESPONSIVE
========================= */

@media (max-width: 640px) {
  .profile-card {
    padding: 20px;
  }
}

/* ===============================
   MOBILE FIRST ( 640px)
================================ */

@media (max-width: 640px) {
  .profile-page {
    margin: 20px auto;
    padding: 0 14px;
  }

  .profile-card {
    padding: 18px;
    border-radius: 12px;
    margin-bottom: 18px;
  }

  .profile-card h2,
  .profile-card h3 {
    font-size: 18px;
  }

  .profile-subtitle,
  .muted {
    font-size: 13px;
  }

  /* Fields */
  .profile-field {
    margin-top: 16px;
  }

  .profile-field label {
    font-size: 12.5px;
  }

  .profile-input,
  .profile-card input,
  .profile-readonly {
    padding: 12px 14px;
    font-size: 14px;
    border-radius: 10px;
  }

  .profile-hint {
    font-size: 11.5px;
  }

  /* Error box */
  .error-box {
    font-size: 12.5px;
    padding: 10px;
  }

  /* Buttons become full-width */
  button {
    width: 100%;
    padding: 12px;
    font-size: 14px;
  }

  button.primary,
  button.danger-btn {
    padding: 12px;
  }

  /* Space between stacked buttons */
  button + button {
    margin-top: 10px;
  }

  /* Danger zone input */
  .profile-card.danger input {
    letter-spacing: 4px;
    font-size: 15px;
  }
}

/* ===============================
   TABLET (641px  1024px)
================================ */

@media (min-width: 641px) and (max-width: 1024px) {
  .profile-page {
    margin: 30px auto;
    padding: 0 18px;
  }

  .profile-card {
    padding: 24px;
  }
}

/* ===============================
   TOUCH & ACCESSIBILITY
================================ */

@media (pointer: coarse) {
  button {
    padding: 14px;
  }

  .profile-input,
  .profile-card input {
    padding: 14px 16px;
  }
}

/* Reduce motion */
@media (prefers-reduced-motion: reduce) {
  button {
    transition: none;
  }
}
