@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap");

.card-suggestions-page {
  font-family: "Inter", system-ui, sans-serif;
  padding: 28px;
  max-width: 1150px;
  margin: 0 auto;
  background: #ffffff;
  color: #0f172a;
}

.card-suggestions-page h2 {
  font-size: 1.9rem;
  font-weight: 800;
  margin-bottom: 24px;
  color: #0f172a;
}


.card-suggestion-box {
  background: linear-gradient(
    180deg,
    #f8fafc,
    #ffffff
  );
  border: 1px solid #e5e7eb;
  border-radius: 18px;
  padding: 22px;
  margin-bottom: 34px;
  box-shadow: 0 10px 28px rgba(15, 23, 42, 0.08);
}

.card-suggestion-box::before {
  content: "Monthly Cashback Analysis";
  display: block;
  font-size: 0.8rem;
  font-weight: 700;
  letter-spacing: 0.08em;
  color: #6366f1;
  margin-bottom: 10px;
}


.card-suggestion-box .row {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-bottom: 14px;
}

.card-suggestion-box label {
  font-size: 0.8rem;
  font-weight: 600;
  color: #475569;
}

.card-suggestion-box select {
  padding: 11px 14px;
  border-radius: 12px;
  border: 1px solid #c7d2fe;
  background: #ffffff;
  font-size: 0.9rem;
  cursor: pointer;
}

.card-suggestion-box select:focus {
  outline: none;
  border-color: #6366f1;
  box-shadow: 0 0 0 4px rgba(99, 102, 241, 0.15);
}


.card-suggestion-box button.primary {
  margin-top: 10px;
  padding: 14px 22px;
  border-radius: 14px;
  border: none;
  background: linear-gradient(
    135deg,
    #22c55e,
    #16a34a
  );
  color: #ffffff;
  font-weight: 700;
  font-size: 0.95rem;
  cursor: pointer;
  box-shadow: 0 10px 26px rgba(34, 197, 94, 0.45);
}

.card-suggestion-box button.primary:hover {
  transform: translateY(-1px);
}

.card-suggestion-box button.primary:disabled {
  opacity: 0.5;
}

.suggestion-results {
  background: linear-gradient(180deg, #eef2ff, #ffffff);
  border: 1px solid #c7d2fe;
  border-radius: 22px;
  padding: 28px;
  margin-bottom: 40px; /* more space before history */
  box-shadow: 0 14px 36px rgba(99, 102, 241, 0.18);
}

.suggestion-results .summary {
  margin: 14px 0 20px;
  font-size: 0.95rem;
  line-height: 1.6;
  color: #1e293b;
}

.suggestion-results .summary.muted {
  color: #64748b;
}



.results-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 18px;
  position: relative;
}

.results-header h3 {
  font-size: 1.3rem;
  font-weight: 800;
  color: #4338ca;
  margin: 0;
}

.close-btn {
  position: absolute;
  top: 0;
  right: 0;
  border: none;
  background: #eef2ff;
  color: #3730a3;
  width: 34px;
  height: 34px;
  border-radius: 10px;
  font-size: 0.95rem;
  cursor: pointer;
}

.close-btn:hover {
  background: #e0e7ff;
}

.info-banner {
  margin: 18px 0;
  padding: 14px 16px;
  border-radius: 14px;
  background: linear-gradient(
    135deg,
    #38bdf8,
    #6366f1
  );
  color: #ffffff;
  font-weight: 600;
  position: relative;
}

.info-banner::after {
  content: "AI Insight";
  position: absolute;
  right: 16px;
  top: 14px;
  font-size: 0.7rem;
  font-weight: 700;
  opacity: 0.85;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 22px;
  margin-top: 18px;
}

.suggestion-card::before {
  content: "Recommended";
  position: absolute;
  top: 14px;
  right: 14px;
  font-size: 0.65rem;
  font-weight: 700;
  color: #16a34a;
}

.rate {
  font-size: 1.15rem;
  font-weight: 800;
  color: #16a34a;
}

.savings {
  font-weight: 600;
  color: #0f766e;
}

.suggestion-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 26px rgba(15, 23, 42, 0.12);
}

.suggestion-card {
  border-radius: 18px;
  padding: 18px 18px 20px;
  background: linear-gradient(180deg, #ffffff, #f8fafc);
  border: 1px solid #e5e7eb;
  box-shadow: 0 10px 26px rgba(15, 23, 42, 0.1);
  position: relative;
  overflow: hidden; /* prevents badge overlap */
}

/* Push title down so badge never overlaps */
.suggestion-card h5 {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 8px;
  padding-right: 80px; /* space for badge */
  line-height: 1.4;
  word-break: break-word;
}

.rotation-note {
  margin-top: 6px;
  font-size: 0.75rem;
  background: #fff7ed;
  border: 1px solid #fed7aa;
  color: #9a3412;
  border-radius: 8px;
  padding: 6px 8px;
}

.suggestion-history h4 {
  font-size: 1.05rem;
  font-weight: 700;
  margin-bottom: 12px;
}

.history-item {
  border-radius: 14px;
  padding: 16px;
  background: linear-gradient(
    180deg,
    #ffffff,
    #f1f5f9
  );
  border: 1px solid #e2e8f0;
}

.history-item.active {
  border-left: 5px solid #6366f1;
  background: #eef2ff;
}

.history-item:hover {
  background: #f8fafc;
}

.delete-btn {
  border: none;
  background: transparent;
  font-size: 1.1rem;
  cursor: pointer;
  opacity: 0.6;
}

.delete-btn:hover {
  opacity: 1;
}

.card-suggestions-locked {
  position: relative;
}

.locked-overlay {
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(8px);
}

.locked-overlay h3 {
  color: #4338ca;
}

.upgrade-btn {
  margin-top: 12px;
  padding: 12px 20px;
  border-radius: 12px;
  border: none;
  background: linear-gradient(135deg, #6366f1, #4f46e5);
  color: #ffffff;
  font-weight: 600;
  cursor: pointer;
}

.blurred {
  filter: blur(4px);
  pointer-events: none;
}

.suggestion-history {
  margin-top: 20px;
}

.suggestion-history h4 {
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: 14px;
  color: #0f172a;
}

.history-item {
  position: relative;
  padding: 16px 18px 18px 18px;
  border-radius: 16px;
  border: 1px solid #e5e7eb;
  background: linear-gradient(
    180deg,
    #ffffff,
    #f8fafc
  );
  margin-bottom: 12px;
  cursor: pointer;
  transition: all 0.15s ease;
}

.history-item:hover {
  box-shadow: 0 12px 26px rgba(15, 23, 42, 0.12);
  transform: translateY(-1px);
}

.history-item.active {
  border-color: #6366f1;
  background: linear-gradient(
    180deg,
    #eef2ff,
    #ffffff
  );
}

.history-item strong {
  font-size: 0.95rem;
  font-weight: 700;
  color: #0f172a;
}

.history-item .small {
  margin-top: 4px;
  font-size: 0.78rem;
  color: #475569;
}

.history-item .muted.small:last-child {
  margin-top: 8px;
  font-size: 0.85rem;
  font-weight: 600;
  color: #16a34a; /* green = money */
}

.delete-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  background: #f1f5f9;
  color: #64748b;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  font-size: 0.9rem;
  cursor: pointer;
  opacity: 0.8;
}

.delete-btn:hover {
  background: #fee2e2;
  color: #dc2626;
  opacity: 1;
}

/* ===============================
   MOBILE FIRST ( 640px)
================================ */

@media (max-width: 640px) {
  .card-suggestions-page {
    padding: 18px 14px;
  }

  .card-suggestions-page h2 {
    font-size: 1.5rem;
    margin-bottom: 18px;
  }

  /* Suggestion input box */
  .card-suggestion-box {
    padding: 16px;
    border-radius: 16px;
    margin-bottom: 26px;
  }

  .card-suggestion-box::before {
    font-size: 0.7rem;
    margin-bottom: 8px;
  }

  .card-suggestion-box select {
    font-size: 0.9rem;
    padding: 12px 14px;
  }

  .card-suggestion-box button.primary {
    width: 100%;
    padding: 14px 0;
    font-size: 0.95rem;
  }

  /* Results container */
  .suggestion-results {
    padding: 18px;
    border-radius: 18px;
    margin-bottom: 30px;
  }

  .suggestion-results .summary {
    font-size: 0.9rem;
  }

  /* Header */
  .results-header h3 {
    font-size: 1.1rem;
  }

  .close-btn {
    width: 32px;
    height: 32px;
    font-size: 0.85rem;
  }

  /* Info banner */
  .info-banner {
    padding: 12px 14px;
    font-size: 0.85rem;
  }

  .info-banner::after {
    font-size: 0.65rem;
    top: 12px;
  }

  /* Card grid  single column */
  .card-grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }

  /* Suggestion card */
  .suggestion-card {
    padding: 16px;
    border-radius: 16px;
  }

  .suggestion-card h5 {
    font-size: 0.95rem;
    padding-right: 70px;
  }

  .rate {
    font-size: 1.05rem;
  }

  /* History */
  .suggestion-history {
    margin-top: 16px;
  }

  .suggestion-history h4 {
    font-size: 1rem;
  }

  .history-item {
    padding: 14px 14px 16px;
    border-radius: 14px;
  }

  .history-item strong {
    font-size: 0.9rem;
  }

  .history-item .small {
    font-size: 0.75rem;
  }

  .delete-btn {
    width: 28px;
    height: 28px;
    font-size: 0.8rem;
  }
}

/* ===============================
   TABLET (641px  1024px)
================================ */

@media (min-width: 641px) and (max-width: 1024px) {
  .card-suggestions-page {
    padding: 22px 18px;
  }

  .card-grid {
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  }

  .suggestion-results {
    padding: 22px;
  }
}

/* ===============================
   TOUCH & ACCESSIBILITY
================================ */

@media (pointer: coarse) {
  .close-btn,
  .delete-btn {
    padding: 6px;
  }

  .card-suggestion-box select {
    padding: 14px 16px;
  }
}

/* Reduce motion */
@media (prefers-reduced-motion: reduce) {
  .suggestion-card,
  .history-item,
  .card-suggestion-box button.primary {
    transition: none;
  }
}
